.container {
  max-width: 1416px;
  margin: 0 auto;
  padding: 0; }

@media (max-width: 1023px) {
  .container {
    max-width: 720px; } }

@media (max-width: 767px) {
  .container {
    max-width: 300px; } }

@font-face {
  font-family: "PT Mono";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

.page-body {
  height: 100%;
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-family: "Roboto Condensed", "Roboto", sans-serif; }

header {
  background: #1b2d37 url("../img/bg@1x.jpg");
  background-blend-mode: multiply;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #ffffff;
  overflow-x: hidden; }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 231px;
  color: #ffffff; }
  .header__wrapper img {
    width: 202px; }

.header__contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 659px; }
  .header__contacts button {
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    background: transparent;
    padding: 14px;
    color: #ffffff;
    border: 1px solid #ffffff;
    border-radius: 5px;
    cursor: pointer; }

.header__contact {
  font-family: "PT Mono";
  font-size: 16px;
  line-height: 18px; }
  .header__contact svg {
    vertical-align: middle;
    display: inline-block; }

@media (max-width: 1023px) {
  .header__wrapper {
    padding-bottom: 116px; }
  .header__contacts {
    min-width: 83px; }
    .header__contacts:first-child svg {
      width: 25px;
      height: 18px; }
    .header__contacts svg {
      width: 24px;
      height: 24px; }
    .header__contacts button,
    .header__contacts span {
      display: none; } }

@media (max-width: 767px) {
  .header__contacts {
    min-width: 72px; }
  .header__wrapper {
    padding-bottom: 231px; }
    .header__wrapper img {
      width: 38px; } }

.main-block {
  display: flex;
  flex-direction: column;
  min-height: 584px;
  position: relative; }
  .main-block::after {
    position: absolute;
    content: "";
    width: 705px;
    height: 515px;
    top: 0;
    right: -30px;
    background: url("../img/circuit.png");
    background-repeat: no-repeat;
    background-size: auto;
    z-index: 1; }
  .main-block::before {
    position: absolute;
    content: "";
    width: 705px;
    height: 670px;
    bottom: 60px;
    right: -20px;
    background: url("../img/triangles.svg");
    background-repeat: no-repeat;
    background-size: auto;
    z-index: 1; }
  .main-block h1 {
    font-weight: bold;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    z-index: 2;
    margin: 0; }
  .main-block p {
    font-size: 22px;
    line-height: 34px;
    font-family: "PT Mono";
    max-width: 683px;
    margin: 0;
    margin-left: 94px;
    margin-top: 30px;
    margin-bottom: 37px;
    z-index: 2;
    position: relative; }
    .main-block p::before {
      position: absolute;
      content: "";
      width: 66px;
      height: 2px;
      top: 17px;
      left: -94px;
      background-color: #ffffff; }

.main-block__button {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  display: block;
  max-width: 407px;
  padding: 17px;
  background: #00b2ff;
  border-radius: 5px;
  margin-left: 94px;
  z-index: 2; }

.main-block__button--mobile {
  display: none; }

.main-block__scroll {
  font-family: "PT Mono";
  font-size: 14px;
  line-height: 16px;
  opacity: 0.5;
  align-self: flex-start;
  margin-top: auto;
  margin-left: -35px;
  margin-bottom: 89px;
  transform: rotate(-90deg);
  z-index: 2; }

@media (max-width: 1023px) {
  .main-block {
    min-height: 500px; }
    .main-block::after {
      width: 465px;
      height: 340px;
      top: -60px;
      right: -200px;
      background-size: contain; }
    .main-block::before {
      width: 505px;
      height: 505px;
      top: -200px;
      right: -180px;
      background-size: contain; }
    .main-block h1 {
      font-size: 60px;
      line-height: 70px;
      max-width: 300px;
      margin-left: 64px; }
    .main-block p {
      font-size: 18px;
      line-height: 28px;
      max-width: 356px;
      margin-left: 115px; }
      .main-block p::before {
        width: 33px;
        height: 2px;
        top: 17px;
        left: -52px; }
  .main-block__button {
    max-width: 351px;
    padding: 17px;
    margin-left: 115px; }
  .main-block__scroll {
    display: none; } }

@media (max-width: 767px) {
  .main-block {
    min-height: auto;
    padding-bottom: 70px; }
    .main-block::after {
      width: 301px;
      height: 220px;
      top: -180px;
      right: -20px; }
    .main-block::before {
      top: -330px;
      right: -350px; }
    .main-block h1 {
      font-size: 40px;
      line-height: 47px;
      max-width: 277px;
      margin-left: 0; }
    .main-block p {
      font-size: 16px;
      line-height: 25px;
      max-width: 245px;
      margin-left: 49px; }
      .main-block p::before {
        width: 33px;
        height: 2px;
        top: 17px;
        left: -48px; }
  .main-block__button {
    display: none; }
  .main-block__button--mobile {
    display: block;
    min-width: 287px;
    margin: 0 auto; } }
